export default {
	name: "category",
	data() {
		return {
			height: 0,
			scrollTop: 0,
			scrollHeight: 0,
			categoryActive: 0,
			activeStyle: {
				color: this.activeTextColor,
				backgroundColor: this.activeBackgroundColor
			},
			imgUrl: '',
			name: ''
		}
	},
	props: {
		//主分类激活索引
		defaultActive: {
			type: Number,
			default: 0
		},
		//主分类List
		categoryList: {
			type: Array,
			default: () => {
				return null;
			}
		},
		//侧边分类List
		subCategoryList: {
			type: Array,
			default: () => {
				return null;
			}
		},
		activeTextColor: {
			type: String,
			default: '#333'
		},
		activeBackgroundColor: {
			type: String,
			default: '#ffffff'
		},
		//主分类点击事件
		categoryMainClick: {
			
		},
		//子分类点击事件
		categorySubClick: {}
	},
	onLoad(e) {
		console.log(this.categoryList)
	},
	methods: {
		categoryClickMain(category, index, name) {
			this.$emit('categoryMainClick',category)
			this.categoryActive = index;
			this.name = name
			this.scrollTop = -this.scrollHeight * index;
		},
		categoryClickSub(category) {
			this.$emit('categorySubClick',category)
		}
	},
	mounted() {
		this.categoryActive = this.defaultActive
		uni.getSystemInfo({
			success: res => {
				this.height = res.screenHeight;
			}
		})
	},
	watch: {
		subCategoryList(newValue, oldValue) {
			
		}
	},
}
